//Home Carousel

.Invest_carousel {
  .carousel_item {
    width: 17rem;
    display: flex;
    // flex-grow: 100;
    flex-direction: column;
    margin: 0 10px;
    background-color: #212428;
    border: 1px solid hsla(0, 0%, 100%, 0.1);
    border-radius: 15px;
    min-height: 470px;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    margin-bottom: 25px;
    background: rgba(255, 255, 255, 0.025);
    transition: 0.7s;
    box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.3);

    &:hover {
      box-shadow: 2px 2px 30px 0px rgba(10, 10, 10, 0.1);
      transition: 0.7s;
      color: #ffffff;
      -webkit-box-shadow: 2px 2px 20px 0px #3bffae;
      -moz-box-shadow: 2px 2px 20px 0px #3bffae;
      box-shadow: 2px 2px 20px 0px #3bffae;
    }

    .author_img {
      width: 50px;
      border-radius: 100%;
      margin: 20px 0 0 20px;
    }
    .nft_img_wrap {
      margin-top: 20px;
      display: flex;
      max-height: 250px;
      justify-content: center;
      align-items: center;

      .nft_img {
        max-height: 210px;
        width: 210px;
        background-size: contain;
        background-position: center;
        border-radius: 7px;
      }
    }
    .nft_item_info {
      display: flex;
      flex-direction: column;
      min-height: 110px;
      justify-content: flex-start;
      margin: 10px 20px 0 15px;
      span {
        .nft_heading {
          cursor: pointer;
          color: #3bffae;
        }

        .nft_heading:hover {
          color: gray;
        }
      }
      .idx_like {
        margin-top: 5px;
        display: flex;
        justify-content: space-between;
      }
    }
  }
}

@media screen and (min-width: 992px) and (max-width: 1199px) {
  .Invest_carousel {
    .carousel_item {
      width: 16rem;

      .author_img {
      }
      .nft_img_wrap {
        width: 220px;
        margin: 0 auto;
        .nft_img {
          width: 190px;
          height: 190px;
        }
      }
    }
  }
}

@media screen and (min-width: 576px) and (max-width: 991px) {
  .Invest_carousel {
    .carousel_item {
      width: 15rem;

      .author_img {
      }
      .nft_img_wrap {
        width: 230px;
        margin: 0 auto;
        .nft_img {
          width: 200px;
          height: 200px;
        }
      }
    }
  }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
  .Invest_carousel {
    .carousel_item {
      width: 15rem;
      .author_img {
      }
      .nft_img_wrap {
        width: 220px;
        .nft_img {
          width: 180px;
          height: 180px;
        }
      }
    }
  }
}
