.Modal {
    margin-top: 30px;
    padding: 20px !important;
    position: absolute;
    top: 30%;
    left: 40%;
}

.heading {
    letter-spacing: 2px;
    word-spacing: 2px;
    margin-top: 30px;
}

.modal-content {
    background-color: #212428;
    margin-top: 50px;
    border-radius: 10px;
    height: 40rem !important;
    width: 30rem !important;
}

.label {
    color: #fff;
    font-size: 18px !important;
    font-weight: 90px !important;
    letter-spacing: 2px;
}

.cross {
    border: none;
    background-color: #3bffae !important;
    border-radius: 100px !important;
    width: 35px !important;
    height: 35px !important;
    size: 40px !important;
    text-align: center;
    position: absolute;
    top: 3%;
    left: 90%;
    font-size: 22px;

}

.cross:hover {
    box-shadow: 2px 2px 20px 0px #3bffae;
    transition: all 0.3s ease;
}

.modal_btn_container {
    display: flex;
    flex-direction: column;
    height: 150px;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 15px
}

.btnOffer {
    border-radius: 5px;
    width: 100%;
    background-color: white;
    height: 35px;
    border: none;
}

.group {
    margin-top: 40px;
}

.item_info_like {
    cursor: pointer;
}

.item_info_like_active i {
    color: pink !important;
}