.BlogBox {
    width: 450px;
    margin: 2px;
    background-color: rgb(231, 230, 230);
}

.BlogBox:hover .blogImg {
    position: relative;
    transform: scale(1.1);
    filter: blur(0.5px);
    transition-duration: 0.3s;
}

.imgSection {
    width: 100%;
    overflow: hidden;
    transition-duration: 0.3s;
}

.BlogMainBtn {
    padding: 5px;
    margin: 5px;
    border: none;
    outline: none;
    border-radius: 5px;
    background-color: #fff;
    color: #7F0000;
    font-weight: bold;
    border: 1px solid #7F0000;
    transition-duration: 0.2s;
}

.BlogMainBtn:hover {
    background-color: #fff;
    color: #7F0000;
    transition-duration: 0.2s;
}

.BlogInnerContent {
    background-color: #7F0000;
    margin: 0px 20px;
    color: #fff;
    padding: 10px;
    position: relative;
    top: -2%;
    border-radius: 5px;
}

.TitleSection {
    font-weight: bold;
}

.BlogShareIcon {
    cursor: pointer;
}

.BlogSocialIcon {
    color: #7F0000;
    margin: 2px 0px;
}

.BlogSocialIconsBox {
    background-color: #fff;
    padding: 8px;
    border-radius: 20px;
    height: 150px;
}

.hideSocialShare {
    display: none;
}