.MuiTableContainer-root {
  width: 100%;
  overflow-x: scroll;
}

.MuiTable-root {
  width: 100%;

  border-spacing: 0;
  border-collapse: collapse;
}

table {
  caption-side: bottom;
  border-collapse: collapse;
  display: table;

  border-collapse: separate;
  box-sizing: border-box;
  text-indent: initial;
  border-spacing: 2px;
  border-color: grey;
}

tbody,
td,
tfoot,
th,
thead,
tr {
  border: 0 solid;
  border-color: inherit;
}

.MuiTableHead-root {
  display: table-header-group;
}

