//Origins Club Carousel & Items
.originsClub_carousel {
  display: flex;
  width: min-content;

  .carousel_item {
    width: 380px;
    display: flex;
    flex-direction: column;
    margin: 0 10px;
    background-color: #212428;
    border: 1px solid hsla(0, 0%, 100%, 0.1);
    border-radius: 15px;
    min-height: 460px;
    -moz-border-radius: 15px;
    -webkit-border-radius: 15px;
    margin-bottom: 25px;
    background: rgba(255, 255, 255, 0.025);
    transition: 0.7s;
    box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.3);

    &:hover {
      box-shadow: 2px 2px 30px 0px rgba(10, 10, 10, 0.1);
      transition: 0.7s;
      color: #ffffff;
      -webkit-box-shadow: 2px 2px 20px 0px #3bffae;
      -moz-box-shadow: 2px 2px 20px 0px #3bffae;
      box-shadow: 2px 2px 20px 0px #3bffae;
    }

    .author_img {
      width: 50px;
      border-radius: 100%;
      margin: 20px 0 0 30px;
    }
    .nft_img_wrap {
      margin-top: 20px;
      display: flex;
      justify-content: center;
      align-items: center;

      .nft_img {
        height: 230px;
        background-size: cover;
        border-radius: 7px;
        width: 230px;
      }
    }
    .nft_item_info {
      display: flex;
      flex-direction: column;
      min-height: 120px;
      justify-content: center;
      margin: 40px 20px 0 25px;
      span {
        .nft_heading {
          cursor: pointer;
          color: #3bffae;
        }

        .nft_heading:hover {
          color: gray;
        }
      }
      .idx_like {
        margin-top: 5px;
      }
    }
  }
}

