.Parent{
    background: rgb(15, 14, 14);
    height: fit-content;
    min-height: 20px;
    margin: 0 15px
    
}
.modal-content {
    height: fit-content !important;
}
.nameinput{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 42%;
    
}

.delInputField{
    background:none   ;
    border: none;

}

.inputField{
    
    background: none;
    height:40px;
    width: 150px;
    padding: 0 10px;
    border-radius: 7px !important;
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border: 0.5px solid rgba(255, 255, 255, 0.1);
    
}

.inputField:focus{
    outline: none;
    border: 0.5px solid rgba(255, 255, 255, 0.1);

   
}
.dell{


    background: none;
   margin: 0 5px 0 3px;
    font-size: 30px;
}

.render{
    background: rgba(252,52,63,.1);
    padding: 5px 10px;
    width: 200px;
    flex-wrap: wrap;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.upperrow{
    margin: 5px;
}
.nft_attr{
    width:150px !important;
    margin: 10px;
    display: block;
    padding: 10px;
    text-align: center;
    background: rgba(252,52,63,.1);
    border: 1px solid rgba(0,0,0,.15);
    border-radius: 5px;
    margin-bottom: 8px;
    text-decoration: none;
    cursor: pointer;
}